:root{--lightgreen:#3cc23f;--lightgray:#E6ECF0;--white:#ffffff;--black:#000000;--lightblue-rgb:221,234,243;--brightblue:#108BE3;--nightblue:#242F3E;--bluishgray:#334557}*{margin:0;padding:0;outline:0;border:none;user-select:none;-webkit-tap-highlight-color:transparent}html{font-family:Verdana,sans-serif}.my-chart-wrapper{position:relative;padding-bottom:20px;transition:background-color .5s}.my-chart-wrapper.night{background-color:var(--nightblue);transition:background-color .5s}.my-chart-wrapper .switch-mode-btn{position:absolute;cursor:pointer;right:20px;top:10px;border:none;background:0 0;color:var(--brightblue);font-size:16px;outline:0;padding:15px 10px}.my-chart-wrapper .charts-container .ruler-container{position:relative;height:50px;margin-left:8px;width:calc(100vw - 27px)}@media (min-width:501px){.my-chart-wrapper .charts-container .ruler-container{margin-left:40px;width:calc(500px - 40px)}}.my-chart-wrapper .charts-container .chart{position:absolute;height:inherit;width:inherit}.my-chart-wrapper .charts-container .window-sizer-wrapper{position:absolute;left:0;height:inherit;width:inherit}.my-chart-wrapper .charts-container .window-sizer-wrapper .window-sizer{cursor:pointer;position:relative;width:100px;height:inherit;border-top:1px solid rgba(var(--lightblue-rgb),.7);border-bottom:1px solid rgba(var(--lightblue-rgb),.7)}.my-chart-wrapper .charts-container .window-sizer-wrapper .window-sizer .sizer{position:absolute;width:6px;height:inherit;background-color:rgba(var(--lightblue-rgb),.7)}@media (max-width:501px){.my-chart-wrapper .charts-container .window-sizer-wrapper .window-sizer .sizer{width:10px}}.my-chart-wrapper .charts-container .window-sizer-wrapper .window-sizer .sizer-right{right:0}.my-chart-wrapper .checkboxes-container{margin:20px 0 20px 30px}@media (max-width:500px){.my-chart-wrapper .checkboxes-container{margin-left:10px}}.my-chart-wrapper .checkboxes-container .category-checkbox{cursor:pointer;position:relative;padding:5px 5px 5px 30px;border:1.5px solid var(--lightgray);border-radius:15px;transition:border .5s}.my-chart-wrapper .checkboxes-container .category-checkbox.night{border-color:var(--bluishgray);transition:border .5s}.my-chart-wrapper .checkboxes-container .category-checkbox:nth-child(2){margin-left:10px}.my-chart-wrapper .checkboxes-container .category-checkbox .checkmark{position:absolute;top:3px;left:5px;width:20px;height:20px;border-radius:50%;border:1px solid;transition:background-color .5s}.my-chart-wrapper .checkboxes-container .category-checkbox .checkmark::after{content:"";position:absolute;width:0;height:0;transform:rotate(90deg);transition:transform .5s}.my-chart-wrapper .checkboxes-container .category-checkbox .category-name{color:var(--black);transition:color .5s}.my-chart-wrapper .checkboxes-container .category-checkbox .category-name.night{color:var(--white);transition:color .5s}.my-chart-wrapper .checkboxes-container .category-checkbox input[type=checkbox]{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.my-chart-wrapper .checkboxes-container .category-checkbox input[type=checkbox]:checked+.checkmark{transition:background-color .5s}.my-chart-wrapper .checkboxes-container .category-checkbox input[type=checkbox]:checked+.checkmark.off{background-color:var(--white)!important}.my-chart-wrapper .checkboxes-container .category-checkbox input[type=checkbox]:checked+.checkmark::after{width:5px;height:8px;top:4px;left:7px;border:solid var(--white);border-width:0 2px 2px 0;transform:rotate(45deg);transition:transform .5s}